.blind {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}
.icoSprite {
  display: block;
  overflow: hidden;
  background-image: url('../assets/sprite_v1.svg');
  background-repeat: no-repeat;
  background-size: 189px 530px;
}

.icoFavi {
  display: block;
  overflow: hidden;
  background-image: url('../assets/favicons-sprite16.png');
  background-repeat: no-repeat;
  background-size: 16px 7038px;
}
